

#home
{
   padding: 80px 10px;
   padding-left: 80px;
   width: 700px;
   /* background-color: rgb(239, 245, 243); */
}

.post-img
{
   width: 100%;
   max-height: 600px;
   object-fit: contain;
}

.profilepic
{
   width:55px;
   height: 55px;
   object-fit: cover;
   border-radius: 50%;
}


.postcard 
{
   margin-bottom: 40px;
   padding: 10px 0px;
   width: 100%;
   border: 1px solid rgb(172, 170, 170);
}
.post-header
{
   display: flex;
   flex-direction: row;
   align-items: center;
   padding: 0px 10px;
   padding-bottom: 10px;
   border-bottom: 1px solid rgb(209, 207, 207);
}

.post-author-info
{
   padding-left: 10px;
   display: flex;
   flex-direction: column;
}

.post-user
{
   font-weight: bold;
   color: black;
}
.post-user-link:hover
{
   text-decoration: none;
}
.post-location
{
   color: rgb(90, 86, 86);
   font-size: 12px;
}

.post-caption
{
   margin-top: 5px;
   /* border: 1px solid rgb(250, 239, 239); */
   padding: 10px 10px;
}
.post-caption pre
{
   white-space: pre-wrap;
   font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body::-webkit-scrollbar {
   display: none;
 }


.addpost-wrapper
{
   /* margin-top: 100px; */
   padding: 80px 10px;
   padding-left: 80px;
  
   text-align: left;
   /* background-color: aqua; */
}

.postcaption
{
   width:80%;
   height:350px;
   padding: 10px 10px;
}

.addpost-submit
{
   margin-top: 10px;
}

.profileimage
{
   width: 300px;
   height: 300px;
   object-fit: cover;
   display: block;
   margin: auto;
   border-radius: 100%;
   border: 3px solid black;
}
.profile-username
{
   text-align: center;
}
.profilewrapper
{
  padding-top: 100px;
}
@media only  screen and (max-width: 600px)
{
   #home
   {
      padding-left: 4px;
      width: 100%;
   }
   .postcard
   {
      width: 100%;
      padding: 10 0px;
   }

   .postcaption
{
   width:100%;
   height:300px;
   
}
.addpost-wrapper
{
   /* margin-top: 100px; */
   padding: 80px 10px;
   padding-left: 10px;
  
   text-align: left;
   /* background-color: aqua; */
}
}