


.login-title
{
    text-align: center;
    margin-top: 100px;
}

.login-wrapper
{
    display: flex;
    justify-content: center;
    margin-top: 100px;
   
}

.inp
{
    margin-top: 18px;
    width: 250px;
    padding: 5px 5px;
}

.loginbtn
{
    display: block;
    margin: auto;
    margin-top: 15px;  
}
span.logerror,.errors
{
    color: red;
    text-align: left;
}

.login-text
{
    margin-bottom: 40px;
}

.navbar
{
    background-color: #860101;
    color: white !important;
    padding-top: 2px;
    padding-bottom: 2px;
    box-shadow: 5px 5px 10px black;
    box-sizing: content-box;
    /* z-index: 1000; */
}
.logout
{
    cursor: pointer;
    /* margin-left: auto; */
    color: rgb(226, 221, 221) !important;
    padding: 4px 10px;
}
.logout:hover
{
    /* color: white !important; */
    color: black !important;
}
.dropdown-menu
{
    background-color: #043253;
}

.navbar-brand
{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.brand 
{
    margin-left: 8px;
}

.user-profile-pic
{
    width: 40px;
    height: 40px;
    object-fit: cover;
    border-radius: 50%;
    margin-left: 5px;
}

.sidebar
{
    position: fixed;
    left: 0;
    height: 100%;
    width:10rem;
    background-color: #043253;
    overflow: hidden;
    padding-top: 100px;
    box-shadow: 5px 5px 10px black;
    z-index: 1000;
    padding-left: 0px;
  
}

.side-comp
{
    width: 100%;
    /* border-radius: 40px; */
    margin-top: 5px;
    display: flex;
    align-items: center;
    color: rgb(211, 200, 200);
    padding: 8px 15px;
    box-sizing: border-box;
    /* background-color: aqua; */
}

.side-link:hover
{
  text-decoration: none;
}

.side-comp:hover
{
    color: white;
    background-color: #032238;
}


.side-icon
{
    width: 2.2rem;
    font-size: large;
}

.side-text
{
   /* color: white; */
    text-align: left;
    font-size: 1.1rem;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: bold;
    margin-left: 8px;
}

.log 
{
    color: black;
}
.signup-btn-w
{
    text-align: right;
    font-size: larger;
    
}
.signuplink
{
    margin-left: 5px;
    color: black !important;
}

.signup-wrapper
{
    margin-top: 100px;
    text-align: center;
}

.signup-wrapper form
{
    text-align: left;
    margin-top: 50px;
}

.container2
{
    margin-top: 70px;
}

@media only  screen and (max-width: 870px) {
    .sidebar {
     display: flex;
     flex-direction:row;
     position: fixed;
     bottom: 0;
     width: 100%;
     height: 40px;
     padding: 0px 10px;
     justify-content: center;
    }

    .side-text
    {
        display: none;
    }
    .side-comp
    {
        height: 100%;
       width: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 5px 5px;
        margin-top: 0px;
    }
    .side-icon
{
    width: 2.2rem;
    font-size: 20px;
}
